import LogoImg from "../../assets/images/logo-white.png";
import { MENU_LIST } from "../../constants/menu";

import "./Footer.scss";

function Footer() {
  return (
    <div className="Footer">
      <div className="container">
        <div className="main-section">
          <div className="logo-container">
            <img className="logo" src={LogoImg} alt="logo" />
          </div>
          <div className="menu-container">
            <p className="list-title">Menu</p>
            <div className="list-container">
              <ul className="list-group">
                <li>
                  <a href="/#"></a>
                </li>
                <li>
                  <a href={MENU_LIST[1].link}></a>
                </li>
                <li>
                  <a href={MENU_LIST[3].link}></a>
                </li>
              </ul>
              <ul className="list-group">
                <li>
                  <a href={MENU_LIST[0].link}></a>
                </li>
                <li>
                  <a href={MENU_LIST[2].link}></a>
                </li>
                <li>
                  <a href={MENU_LIST[4].link}></a>
                </li>
              </ul>
            </div>
          </div>
          <div className="office-information-container">
            <p className="list-title">Office Information</p>
            <ul className="office-information">
              <li>883</li>
              <li>(02) 888-5678 </li>
              <li>meowoffice@linmeow.tw</li>
            </ul>
          </div>
        </div>
        <p className="copyright">
           2023 Miao Li-Han 
        </p>
      </div>
    </div>
  );
}

export default Footer;
